#media {
  background: url('../../assets/images/golf_green2.jpg');
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  max-height: 100%;
  padding-top: 6rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  .video-div {
    color: var(--secondary-color);
    background-color: var(--yellow-color);
    display: flex;
    flex-direction: row;
    width: 90%;
    justify-content: space-between;
    margin: 1rem;
    align-items: center;
    h3 {
      width: 20%;
    }
    :last-child {
      width: 70% !important;
    }
    @media screen and(max-width: 450px) {
      flex-direction: column;
      h3 {
        width: 100%;
      }
    }
  }
  :last-child {
    margin-bottom: 100px;
  }
}
