#cheat{
    background: url('../../assets/images/chipping_green.jpg');
    background-size: cover;
    background-position: center;
    min-height: 100vh;

    .background {
        background-color: rgba(4, 57, 39, 0.85);
        min-height: 100vh;
      }

      .p-text {
        font-size: 2.15rem;
        font-weight: 500;
        text-align: center;
        text-justify: inter-word;
        color: var(--white-color);
        text-shadow: -1px 0 black;
        text-decoration: none;
        line-height: 1.5;
        padding: 8rem 1rem 1rem 1rem;
    
        @media screen and (max-width: 900px) {
          font-size: 1.55rem;
          margin: 0 15px;
        }
    
        @media screen and (max-width: 450px) {
          font-size: 1rem;
          margin: 10px;
        }
      }
}